:root {
  --heading-text-color: #2d2d41;
  --list-item-bg: #f8f8f8;
  --active-item: #87b9e7;
  --active-item-text: #ffffff;
  --border: #f4f5f8;
  --submit-btn: #517da3;
  --text: #5e6575;
  --main-bg: #e5e5e5;
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

body {
  background: var(--main-bg);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    padding: 30px;
  }

  .form-container {
    background: var(--active-item-text);
    padding: 10px;
  }

  .heading {
    font-family: Playfair Display;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -0.333333px;
    color: var(--heading-text-color);
    margin-top: 0;
    margin-bottom: 20px;
  }

  .title {
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.333333px;
    color: var(--heading-text-color);
    margin-top: 0;
    margin-left: 5px;
  }

  .list {
    margin-top: 0;
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-direction: column;
  }

  .list-item {
    width: 240px;
    height: 50px;
    background: var(--list-item-bg);
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--heading-text-color);
    letter-spacing: -0.333333px;
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
  }

  .list .list-item:not(:last-child) {
    margin-bottom: 10px;
  }

  .radio {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    position: absolute;
  }

  .chosen {
    background: var(--active-item);
    color: var(--active-item-text);
    text-decoration-line: underline;
  }

  .validation-form {
    display: flex;
    flex-direction: column;
    width: 240px;
  }

  .form-input {
    width: 218px;
    border: 1px solid var(--border);
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    height: 25px;
  }

  .form-label {
    position: relative;
    top: -30px;
    left: 10px;
    font-family: Raleway;
    color: var(--text);
    font-size: 14px;
    line-height: 20px;
  }

  .form-input:focus,
  .textarea:focus {
    outline-color: var(--active-item);
  }

  .form-input:focus + .form-label,
  .form-input:not(:placeholder-shown) + .form-label {
    position: relative;
    top: -47px;
    left: 15px;
    background: var(--active-item-text);
    transition: 500ms var(--timing-function);
    color: var(--text);
  }

  .textarea {
    width: 240px;
    height: 95px;
    resize: none;
    border: 1px solid var(--border);
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    box-sizing: border-box;
  }

  .comment {
    position: relative;
    top: -90px;
    left: 10px;
    font-family: Raleway;
    color: var(--text);
    font-size: 14px;
    line-height: 20px;
  }

  .textarea:focus + .comment,
  .textarea:not(:placeholder-shown) + .comment {
    position: relative;
    top: -108px;
    left: 15px;
    background: var(--active-item-text);
    color: var(--text);
    transition: 500ms var(--timing-function);
  }

  .submitBtn {
    width: 240px;
    height: 45px;
    border: none;
    background-color: var(--submit-btn);
    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: var(--active-item-text);
    margin-bottom: 10px;
    cursor: pointer;
  }
}

